import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';
import { MDBContainer } from "mdbreact";
import TabsLayout from './TabsLayout';

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <MDBContainer className="App">
          <TabsLayout />
        </MDBContainer>
      </BrowserRouter>
    );
  }
}

export default App;
